body {
  margin: 0;
  height: 100%;
  font-family: 'Motiva', sans-serif
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.App {

  height:  100%;
}

.text:hover {
  cursor: pointer ;
}

.image {
  -webkit-user-select: none;
  -o-user-select: none;
  user-select: none;
  font-family: 'Motiva.Art Medium' !important;
}

.image1 {
  font-family: 'Motiva.Art Medium' !important;
}

.image2 {
  font-family: 'Motiva.Art Medium' !important;
}

@font-face {
  font-family: 'Motiva';
  src: local('Motiva-Regular'), url(/xor-demo/static/media/Motiva-Regular.f28762f1.otf) format('opentype');
}

@font-face {
  font-family: 'Motiva.Art Medium';
  src: local('Motiva.Art Medium'), url(/xor-demo/static/media/Motiva.Art-Medium.0215a6fd.otf) format('opentype');
}

